import React from "react";

const Dashboard = React.lazy(() => import("./views/dashboard/Dashboard"));
const Users = React.lazy(() => import("./views/users/Users"));
const User = React.lazy(() => import("./views/users/User"));

/**
 * Master Wilayah Adminsitrasi
 */
const Provinsi = React.lazy(() =>
  import("./views/master-wil/provinsi/Provinsi")
);
const Kabupaten = React.lazy(() =>
  import("./views/master-wil/kabupaten/Kabupaten")
);
const Kecamatan = React.lazy(() =>
  import("./views/master-wil/kecamatan/Kecamatan")
);

const Pegawai = React.lazy(() => import("./views/master/Pegawai"));
const Variabel = React.lazy(() => import("./views/master/Variabel"));
const Indikator = React.lazy(() => import("./views/master/Indikator"));
const Pertanyaan = React.lazy(() => import("./views/master/Pertanyaan"));
const Bobot = React.lazy(() => import("./views/master/Bobot"));
const Organisasi = React.lazy(() => import("./views/master/Organisasi"));
const PegawaiKecamatan = React.lazy(() =>
  import("./views/master/PegawaiKecamatan")
);
const Jabatan = React.lazy(() => import("./views/master/Jabatan"));

/**
 * setting application
 */
const UserPengguna = React.lazy(() => import("./views/setting-app/User"));
const MenuApp = React.lazy(() => import("./views/setting-app/Menu"));
const Settings = React.lazy(() => import("./views/setting-app/Settings"));
/**
 * Transaction Monev
 */
const AddMonev = React.lazy(() => import("./views/monev/AddMonev"));
const ReportMonev = React.lazy(() => import("./views/monev/ReportMonev"));
const routes = [
  { path: "/", exact: true, name: "Home" },
  { path: "/dashboard", name: "Dashboard", component: Dashboard },

  /**
   * Master Wilayah
   * 1. Provinsi
   * 3. Kabupaten
   * 2. Kecamatan
   */

  {
    path: "/master-wil/",
    name: "Wilayah Adminsitrasi",
    component: Provinsi,
    exact: true,
  },
  { path: "/master-wil/provinsi", name: "Provinsi", component: Provinsi },
  { path: "/master-wil/kabupaten", name: "Kabupaten", component: Kabupaten },
  { path: "/master-wil/kecamatan", name: "Kecamatan", component: Kecamatan },

  /**
   * Master
   */
  { path: "/master/", name: "Master", component: Pegawai, exact: true },
  { path: "/master/pegawai", name: "Pegawai", component: Pegawai },
  { path: "/master/variabel", name: "Variabel", component: Variabel },
  {
    path: "/indikator/:id",
    exact: true,
    name: "Indikator",
    component: Indikator,
  },
  {
    path: "/master/pertanyaan",
    exact: true,
    name: "Pertanyaan",
    component: Pertanyaan,
  },
  { path: "/master/bobot", name: "Bobot", component: Bobot },
  { path: "/master/organisasi", name: "Bobot", component: Organisasi },
  {
    path: "/master/pegkecamatan",
    name: "Pegawai Kecamatan",
    component: PegawaiKecamatan,
  },
  { path: "/master/jabatan", name: "Jabatan", component: Jabatan },

  /**
   * Setting Application
   */
  {
    path: "/setting-app/",
    name: "Pengaturan Aplikasi",
    component: UserPengguna,
    exact: true,
  },
  {
    path: "/setting-app/users",
    name: "Manajemen Pengguna",
    component: UserPengguna,
  },
  {
    path: "/setting-app/menu",
    name: "Manajemen Menu Aplikasi",
    component: MenuApp,
  },
  {
    path:"/setting-app/settings",
    name: "Konfigurasi Aplikasi",
    component:Settings
  },

  /**
   * Transaction Monev
   * Form Input
   * Report Monev
   */
  { path: "/monev", name: "Monev", component: ReportMonev, exact: true },
  { path: "/monev/ReportMonev", name: "Laporan Monev", component: ReportMonev },
  { path: "/monev/AddMonev", name: "Add Monev", component: AddMonev },
  { path: "/users", exact: true, name: "Users", component: Users },
  { path: "/users/:id", exact: true, name: "User Details", component: User },
];

export default routes;
